* {
	box-sizing: border-box; }
a {
	text-decoration: none; }
.round1, .round1:before, .round1:after {
	border: 3px solid transparent;
	border-radius: 50%; }
.round1 {
	width: 150px;
	height: 150px;
	border-top-color: #42598a;
	animation: spin 1s infinite linear;
	position: absolute;
	top: 50%;
	left: 50%;
	margin-left: -75px;
	margin-top: -75px;
	z-index: 1001;
	&:before, &:after {
		content: '';
		position: absolute; }
	&:before {
		top: 10px;
		left: 10px;
		right: 10px;
		bottom: 10px;
		border-top-color: $yellow;
		animation: spin 2s infinite linear; }
	&:after {
		top: 23px;
		left: 23px;
		right: 23px;
		bottom: 23px;
		border-top-color: #4aa153;
		animation: spin 3s infinite linear; } }
@keyframes spin {
	from {
		transform: rotate(0deg); }
	to {
		transform: rotate(360deg); } }
.preload-block {
	position: fixed;
	width: 100%;
	height: 100%; }
.preload-block__left, .preload-block__right {
	position: absolute;
	background: #222;
	width: 51%;
	height: 100%;
	z-index: 1000;
	transition: all .7s;
 .preload-block__left {
 	left: 0; } }
.preload-block__right {
	right: 0; }
.loaded {
	.round1 {
		display: none; }
	.preload-block__left {
		transform: translateX(-100%); }
	.preload-block__right {
		transform: translateX(100%); } }

.h2 {
	font: 300 30px/1em "Din Light", sans-serif;
	color: $main-text-color;
	text-transform: uppercase;
	text-align: center;
	@include r(767) {
		font-size: 18px; } }
.h2--white {
	color: $white; }
.phone {
	color: $white;
	font: 700 36px/0.8em $DB;
	@include r(767) {
		font-size: 18px; } }
.phone--small {
	font-size: 30px; }
.mail {
	color:  $yellow;
	font: 300 24px/1em "Din Light", sans-serif;
	@include r(767) {
		font-size: 16px; } }
.is-night-js {
	.mail {
		color: $orange-darken; } }
.article {
	color: $main-text-color;
	font: $main-text-font;
	@include r(767) {
		font: $main-text-font-mobile; } }
.article__img {
	width: 475px;
	height: 250px;
	display: block;
	@include cover;
	margin-left: auto;
	margin-right: auto;
	@include r(1200) {
		width: 300px;
		height: 180px; }
	@include r(767) {
		width: 270px;
		height: 130px; } }
.article--two-column {
	@include clr; }
.article__side {
	width: 475px;
	float: left;
	&:nth-of-type(odd) {
		.article__img {
			margin-top: 67px;
			@include r(991) {
				margin-top: 15px; } } }
	@include r(1200) {
		width: 440px; }
	&:nth-of-type(even) {
		float: right;
		.article__img {
			margin-bottom: 67px;
			@include r(991) {
				margin-bottom: 15px; } } }
	@include r(991) {
		width: 100%;
		float: none;
		margin-bottom: 10px; } }
%red-text-underline {
	color: #ab5c4e;
	text-decoration: underline;
	font: $main-text-font; }
.is-hidden-js {
	overflow: hidden; }
sup {
	font-size: 10px;
	display: inline-block;
	vertical-align: top; }

.slick-prev, .slick-next {
	&:before {
		opacity: 1; } }
.slick-prev {
	&:before {
		@include arr(50, 50, #33997f, l); } }
.slick-next {
	&:before {
		@include arr(50, 50, #33997f, r); } }
.is-night-js {
	.slick-prev {
		&:before {
			@include arr(50, 50, #586d98, l); } }
	.slick-next {
		&:before {
			@include arr(50, 50, #586d98, r); } } }
