.gallery {
	padding-top: 50px;
	padding-bottom: 43px;
	@include r(767) {
		padding-bottom: 20px; } }
.gallery__h2 {
	margin-bottom: 32px; }
.slider__img-block {
	width: 748px;
	height: 400px;
	@include cover;
	@include r(1100) {
		width: 100%; }
	@include r(767) {
		height: 300px; } }
.slider__item {
	position: relative;
	margin: 50px -40px;
	@include r(1100) {
		margin: 0; } }
.slick-center {
	margin: 10px -40px 0;
	z-index: 222;
	height: 480px;
	@include r(1100) {
		display: none; }
	.slider__img-block {
		height: 480px;
		&:hover {
			cursor: url('../img/cursor.png'), auto; } } }
.slider-wrapper {
	.slick-prev {
		left: 0; }
	.slick-next {
		right: 0; } }
